import React, { Component, Fragment } from 'react';
import Quote from '../../components/Quote/Quote';
// import Navigation from '../../components/Navigation/Navigation';
import ReadButton from '../../components/ReadButton/ReadButton';
import Rating from '../../components/Rating/Rating';
// import PreviousQuotesButton from '../../components/PreviousQuotesButton/PreviousQuotesButton';
// import YodaButton from '../../components/YodaButton/YodaButton';

const escapeChars = { lt: '<', gt: '>', quot: '"', apos: "'", amp: '&' };

function unescapeHTML(str) {//modified from underscore.string and string.js
    return str.replace(/\&([^;]+);/g, function(entity, entityCode) {
        var match;

        if ( entityCode in escapeChars) {
            return escapeChars[entityCode];
        } else if ( match = entityCode.match(/^#x([\da-fA-F]+)$/)) {
            return String.fromCharCode(parseInt(match[1], 16));
        } else if ( match = entityCode.match(/^#(\d+)$/)) {
            return String.fromCharCode(~~match[1]);
        } else {
            return entity;
        }
    });
}

class Content extends Component {
  state = {
    title: null,
    content: null
  };

  async getQuote() {
    if (!navigator.onLine) { 
      const { title, content } = localStorage.getItem('quoteData')
      this.setState({ title, content });
    } else {
      const quoteReq = await fetch('http://quotesondesign.com/wp-json/posts?filter[orderby]=rand&filter[posts_per_page]=1');
      const { title, content } = JSON.parse(await quoteReq.text())[0];
      this.setState({ title, content });
    }
  }

  async componentDidMount() {
    this.getQuote();
  }

  handleClick = (type) => {
    if (type === 'good') {
      console.log('good');
    } else if (type === 'meh') {
      console.log('meh');
    } else if (type === 'bad') {
      console.log('bad');
    }
    
    this.getQuote();
  }
    
  render() {
      const { title, content } = this.state;
      return (
        <Fragment>
        <Quote title={title} content={content} />
        <Rating onClick={this.handleClick} />
          <ReadButton quote={content} />
          {/* <PreviousQuotesButton /> */}
          {/* <YodaButton /> */}
        </Fragment>
      );
    }
}

export default Content


