import React, { Component } from 'react';
import ProfilePicture from '../../components/ProfilePicture/ProfilePicture';
import Introduction from '../../components/Introduction/Introduction';
import { Row, Col } from 'antd';

import './Header.module.css';

class Header extends Component {
  state = {
    ip: null,
    latitude: null,
    longitude: null,
    city: null
  };

  async componentDidMount() {
    const locationReq = await fetch('http://api.ipstack.com/check?access_key=b4e0e79e762dda7bf49b4ae66f48c848&format=1');
    const { ip, latitude, longitude, city } = JSON.parse(await locationReq.text());
    this.setState({ ip, latitude, longitude, city });
  }
    render() {
      const { ip, latitude, longitude, city } = this.state;
      return (
        <header
          className="app-header"
          style={{backgroundImage: `url('https://api.mapbox.com/styles/v1/mapbox/streets-v10/static/${longitude},${latitude},13.0,0,0/${window.innerWidth}x300?access_token=pk.eyJ1IjoibXppZW1lciIsImEiOiJjamsza3ZjaGoxM2hwM3FudHJ0M2lxaTVkIn0.ZfHuHBQaDA6AN5TNP62a_w')`}}
        >
          <Row type="flex" justify="space-around" align="middle">
            <Col span={24}><ProfilePicture ipAddress={ip} /></Col>
            <Col span={24}><Introduction city={city} /></Col>
          </Row>
        </header>
      );
    }
}

export default Header;


