import React from 'react';
import { Avatar } from 'antd';

import './ProfilePicture.module.css';

const ProfilePicture = ({ipAddress}) => (
    <div className="profileOuter">
        {ipAddress ?
            <Avatar size="large" src={`https://robohash.org/${ipAddress}.png?bgset=bg2`} /> :
            <Avatar size="large" icon="user" />
        }
    </div>
);

export default ProfilePicture;


