import React, { Component } from 'react';
import { Button } from 'antd';

class Rating extends Component {
    handleClick = ({target}) => {
        if (target.querySelector('.anticon-smile')) {
            this.props.onClick('good');
        } else if (target.querySelector('.anticon-meh-o')) {
            this.props.onClick('meh');
        } else if (target.querySelector('.anticon-meh')) {
            this.props.onClick('bad');
        } 
    }

    render() {
        return (
        <div>
            <Button onClick={this.handleClick} size="large" icon="smile" />
            <Button onClick={this.handleClick} size="large" icon="meh-o" />
            <Button onClick={this.handleClick} size="large" icon="meh" />
        </div>
        );
    }
}

Rating.defaultProps = {
    onClick: () => {}
}

export default Rating;





