import React, { Fragment } from 'react';
import './Quote.module.css';

const Quote = ({title, content}) => (
  <Fragment>
    <h3>{title}</h3>
    <span className="quote" dangerouslySetInnerHTML={{ __html: content }} />
  </Fragment>
);

export default Quote;


